  font-size: 0.9rem;
  color: #334155;
  line-height: 1.5;
  margin-bottom: 1.25rem;
}

/* Image styling */
.block-section .block-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 1rem;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}
.block-section .block-image:hover {
  transform: scale(1.02);
}

/* Label (e.g. "Article", "Date") */
