}
.block-section h3 a:hover, .block-section .h3 a:hover {
  color: #660000;
  text-decoration: none;
}

/* Optional main text below the subheading */
.block-section p.block-main-text {
  font-size: 0.95rem;
  margin: 0 0 1.25rem;
  line-height: 1.5;
  color: #334155;
