.hover-magnetic {
  position: relative;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.hover-magnetic:hover {
  transform: translate(var(--mouse-x), var(--mouse-y));
}

.hover-glitch {
  position: relative;
}
.hover-glitch:hover {
  animation: glitch 0.3s ease-in-out;
}
.hover-glitch:hover::before, .hover-glitch:hover::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hover-glitch:hover::before {
  animation: glitch-1 0.3s ease-in-out;
  color: #990000;
  z-index: -1;
}
.hover-glitch:hover::after {
  animation: glitch-2 0.3s ease-in-out;
  color: #8b2331;
  z-index: -2;
}

@keyframes glitch {
  0%, 100% {
    transform: translate(0);
  }
  20% {
    transform: translate(-2px, 2px);
  }
  40% {
    transform: translate(-2px, -2px);
  }
  60% {
    transform: translate(2px, 2px);
  }
  80% {
    transform: translate(2px, -2px);
  }
}
@keyframes glitch-1 {
  0%, 100% {
    clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
    transform: translate(-2px);
  }
  20% {
    clip-path: polygon(0 15%, 100% 15%, 100% 20%, 0 20%);
  }
  40% {
    clip-path: polygon(0 40%, 100% 40%, 100% 45%, 0 45%);
  }
  60% {
    clip-path: polygon(0 70%, 100% 70%, 100% 75%, 0 75%);
  }
  80% {
    clip-path: polygon(0 90%, 100% 90%, 100% 95%, 0 95%);
  }
}
@keyframes glitch-2 {
  0%, 100% {
    clip-path: polygon(0 5%, 100% 5%, 100% 10%, 0 10%);
    transform: translate(2px);
  }
  20% {
    clip-path: polygon(0 30%, 100% 30%, 100% 35%, 0 35%);
  }
  40% {
    clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
  }
  60% {
    clip-path: polygon(0 60%, 100% 60%, 100% 65%, 0 65%);
  }
  80% {
    clip-path: polygon(0 80%, 100% 80%, 100% 85%, 0 85%);
  }
}
.hover-flip {
